import React from 'react';

const SearchBar = (props) => {
    return (
        <form onSubmit={props.handleSubmitLink} style = {{display: 'flex'}}>
            <input className="demo-input" name="search" aria-label="Tm kim" placeholder="Nhp tn  tm kim sch"></input>
            <button className="demo-button">Tm kim</button>
        </form>
    )
}
export default SearchBar;